.modest {
	background-color: #fff;
	border-bottom: 1px solid #dde1e4;
}

.modest__wrap {
	padding-top: 80px ;
	padding-bottom: 100px;
}

.modest__title {	
	font-size: 36px;
	font-family: "Quattrocento Sans";
	color: rgb(41, 47, 54);
	font-weight: bold;
	text-transform: uppercase;
	line-height: 1.167;
	margin-left: 20px;
	margin-top: 0;
	position: relative;
	
	&::after {
		content: '';
		position: absolute;
		width: 10px;
		height: 35px;
		top: 0;
		left: -20px;
		background-color: #e1e6ea;
		display: block;
	}
}

.modest__block {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	margin-top: 60px;
}

.modest__left {
	flex-basis: 53%;
	margin-right: 20px;
}

.modest__text {
	font-size: 16px;
	color: rgb(141, 143, 146);
	line-height: 1.625;	
	margin-top: 0;
	margin-bottom: 35px;
}

.modest__list {
	display: flex;
	justify-content: start;
	list-style: none;
	padding: 0;
	margin: 0;
	margin-top: 50px;
}

.modest__element {
	display: flex;
	justify-content: center;
	align-items: center;	
	background-color: rgb(200, 205, 208);
	border-right: 1px solid #fff;
	width: 60px;
	height: 60px;
	transition-property: background-color;
	transition-duration: 500ms;
	cursor: pointer;

	&:hover {
		background-color: lighten(rgb(200, 205, 208), 4%);
	}
}

.modest__link {
	font-size: 36px;
	font-family: 'Socialico';
	color: rgb(255, 255, 255);
	text-transform: uppercase;
	line-height: 1.167;
	text-decoration: none;
}


.modest__right {
	flex-basis: 43%;
	flex-direction: column;
	justify-content: flex-end;	
}

.modest__item {
	display: flex;
	margin-bottom: 23px;

	&:last-child {
		margin-bottom: 0;
	}
}

.modest__img {
	display: flex;
	align-items: center;
	justify-content: center;
	margin-right: 25px;
	width: 50px;
	height: 50px;
	background-color: #23b7a4;
}

.modest__number {
	font-size: 20px;
  color: white;
}


.modest__desc {
	flex-basis: 85%;
}

.modest__name {
	font-size: 18px;
	font-family: "Quattrocento Sans";
	color: rgb(41, 47, 54);
	font-weight: bold;
	text-transform: uppercase;
	margin-top: 0;
	margin-bottom: 10px;
}

.modest__subname{
	font-size: 16px;	
	color: rgb(141, 143, 146);
	line-height: 1.625;
	margin-top: 0;	
}

@media (max-width: 768px) {
	.modest__wrap {
		flex-direction: column;
		justify-content: space-between;
		padding-top: 40px;
		padding-bottom: 50px;
	}

	.modest__block {
		margin-top: 0;
	}

	.modest__left {
		flex-basis: 100%;
		margin-right: 0;
	}

	.modest__right {
		flex-basis: 100%;
		margin-top: 60px;
	}

	.modest__list {
		justify-content: center;
	}
}