.team {
	background-color: #fff;
}

.team__wrap {
	padding-top: 80px;
	padding-bottom: 100px;
}

.team__title {	
	font-size: 36px;
	font-family: "Quattrocento Sans";
	color: rgb(41, 47, 54);
	font-weight: bold;
	text-transform: uppercase;
	line-height: 1.167;
	margin-left: 20px;
	margin-top: 0;
	position: relative;

	&::after {
		content: '';
		position: absolute;
		width: 10px;
		height: 35px;
		top: 0;
		left: -20px;
		background-color: #e1e6ea;
		display: block;
	}	
}

.team__list {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	margin-top: 25px;
}

.team__element {
	// flex-basis: 23%;
	flex-basis: 262px;
	margin-top: 30px;
	padding: 5px;
	&:last-child {
		margin-right: 0;
	}

	img {
		display: block;
		// width: 100%;
	}
	
	&:hover {
		.team__photo::before {
			opacity: 0.4;
		}

		.team__socials {
			opacity: 1;
		}

		.team__info {
			background-color: #3a454d;
		}

		.team__name {
			color: #fff;
		}

		.team__subname {
			color: #fff;
		}
	}
}

.team__photo {
	display: flex;
	justify-content: center;
	align-items: center;
	position: relative;

	&::before {
		content: '';
		position: absolute;
		display: block;
		// width: 100%;
		// height: 100%;
		background-color: rgb(255, 255, 255);
		opacity: 0.4;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		opacity: 0;
		transition-property: opacity;
	  transition-duration: 500ms;
	}
}

.team__img {
	max-width: 100%;
	height: auto;
	display: block;
	overflow: hidden;
}

.team__socials {
	position: absolute;
	display: flex;
	list-style: none;
	padding: 0;
	margin: 0;
	opacity: 0;
	transition-property: opacity;
	transition-duration: 500ms;
}

.team__like {
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: rgb(58, 69, 77);
	border-right: 1px solid #fff;
	width: 50px;
	height: 50px;
	transition-property: background-color;
	transition-duration: 500ms;
	cursor: pointer;

	&:last-child {
		border-right: none;
	}

	&:hover {
		background-color: lighten(rgb(58, 69, 77), 5%);
	}
}

.team__link {
	font-size: 28px;
	font-family: 'Socialico';
	color: rgb(255, 255, 255);
	text-transform: uppercase;
	line-height: 1.167;
	text-decoration: none;
}

.team__info {
	background-color: #e1e6ea;
	padding: 15px;
	transition-property: background-color;
	transition-duration: 500ms;
}

.team__name {
	font-size: 16px;
	font-family: "Quattrocento Sans";
	color: rgb(41, 47, 54);
	font-weight: bold;
	text-transform: uppercase;
	line-height: 1.187;
	text-align: left;
	margin: 0;
	transition-property: color;
	transition-duration: 500ms;	
}

.team__subname {
	font-size: 14px;		
	color: rgb(141, 143, 146);
	line-height: 1.857;
	margin: 0;
	transition-property: color;
	transition-duration: 500ms;			
}

@media (max-width: 1120px) {

	.team__list {
		justify-content: space-evenly;
	}	
}

@media (max-width: 768px) {
	.team__wrap {
		padding-top: 40px;
		padding-bottom: 50px;
	}
}

@media (max-width: 480px) {

	.team__list {
		margin-top: 0;
	}
	.team__element {
		margin-top: 40px;
		padding: 0;
	}
}
	