.menu {
	display: flex;
	flex-wrap: wrap;
	list-style: none;
}

.menu__element {
	margin-right: 15px;
	border: 2px solid transparent;
	border-radius: 5px;
	padding: 0 10px;
	transition-duration: 500ms;
	transition-property: border-color;	

	&:hover {
		border: 2px solid #5b6168;
	}

	&.active {
		border: 2px solid #23b7a4;
	}
}

.menu__link {
	font-size: 14px;
	font-family: "Open Sans Semibold";
	color: #a4aead;
	text-transform: uppercase;
	line-height: 1.8;
	display: inline-block;
	text-decoration: none;
	position: relative;
	transition-duration: 500ms;
	transition-property: color;

	&.active {
		color: #23b7a4; 
	}
}

@media (max-width: 980px) {

	.market {
		.market__wrap {
			padding-top: 120px;
			padding-bottom: 150px;
		}
	}
	
	.menu__element {
		margin-right: 5px;
	}
}

@media (max-width: 768px) {
	.header__wrap {
		flex-direction: column;
		justify-content: space-between;
		padding: 15px;
	}

	.header__logo {
		margin-bottom: 10px;
	}
}


@media (max-width: 480px) {

	.market {
		.market__wrap {
			padding-top: 60px;
			padding-bottom: 75px;
		}
	}
	
	.menu__element {
		margin-right: 5px;
	}
}