.features {
	background-color: #3a454d;
}

.features__wrap {
	padding-top: 80px;
	padding-bottom: 110px;
}

.features__title {
	font-size: 36px;
	font-family: "Quattrocento Sans";
	color: rgb(255, 255, 255);
	font-weight: bold;
	text-transform: uppercase;
	line-height: 1.167;
	margin-left: 20px;
	position: relative;
	
	&::after {
		content: '';
		position: absolute;
		width: 10px;
		height: 35px;
		top: 0;
		left: -20px;
		background-color: #4e5a62;
		display: block;
	}		
}

.features__list {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	margin-top: 20px;
}

.features__element {
	display: flex;
	flex-direction: column;
	text-align: left;
	// flex-basis: 31.58%;
	flex-basis: 360px;
	margin-left: 5px;
	margin-right: 5px;
	margin-top: 40px;
}

.features__img {
	display: block;
	max-width: 100%;
	height: auto;
}

.features__name {
	font-size: 18px;
	font-family: "Quattrocento Sans";
	color: rgb(255, 255, 255);
	font-weight: normal;
	text-transform: uppercase;
	line-height: 2.333;
	margin: 55px 0 30px 0;
}

.features__desc {
	font-size: 16px;
	color: rgb(220, 223, 225);
	line-height: 1.625;
	margin: 0;	
}

@media (max-width: 1140px) {

	.features__list {
		justify-content: space-evenly;
	}	
}

@media (max-width: 768px) {
	.features__wrap {
		padding-top: 40px;
		padding-bottom: 50px;
	}

	.features__name {
		margin: 30px 0 20px;
	}
}

@media (max-width: 480px) {

	.features__element {
		margin-right: 0;
		margin-left: 0;
	}

	.features__list {
		margin-top: 0;
	}
}