.market {
	background: url('../img/market__bg.jpg') center top no-repeat;
	background-size: cover;
}

.market__wrap {
	padding-top: 220px;
	padding-bottom: 250px;
}

.market__info {
	max-width: 655px;
	margin-right: auto;
}

.market__title {
	font-size: 36px;
	font-family: "Quattrocento Sans";
	color: #fff;
	font-weight: bold;
	text-transform: uppercase;
	line-height: 1.167;	
	margin: 0;
}

.market__text {	
	font-size: 16px;
	color: #ccd0d7;
	line-height: 1.625;
	margin: 30px 0;		
}

.market__button {
	@include button() {
		display: inline-block;
		margin-top: 20px;
		margin-bottom: 20px;
		margin-right: 10px;
	};
}

