.work {
	background-color: #3a454d;
}

.work__wrap {
	padding-top: 75px;
	padding-bottom: 60px;
	text-align: center;
}

.work__title {	
	font-size: 36px;
	font-family: "Quattrocento Sans";
	color: rgb(255, 255, 255);
	font-weight: bold;
	text-transform: uppercase;
	line-height: 1.167;
	margin-left: 20px;
	margin-top: 0;
	position: relative;
	text-align: left;

	&::after {
		content: '';
		position: absolute;
		width: 10px;
		height: 35px;
		top: 0;
		left: -20px;
		background-color: #4e5a62;
		display: block;
	}		
}

.work__list {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	margin-top: 25px;
}

.work__element {
	// flex-basis: 31.58%;
	flex-basis: 360px;
	padding: 30px 5px 5px 5px;

	&:hover {

		.work__photo::before {
			opacity: 0.502;
		}

		.work__plus {
			opacity: 1;
		}
		
		.work__info {
			opacity: 1;
		}
	}
}

.work__photo {
	display: flex;
	justify-content: center;
	position: relative;
	cursor: pointer;


	&::before {
		content: '';
		position: absolute;
		display: block;
		width: 100%;
		height: 100%;
		background-color: rgb(41, 47, 54);
		opacity: 0.502;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		opacity: 0;
		transition-property: opacity;
	  transition-duration: 500ms;
	}
}

.work__img {
	max-width: 100%;
	height: auto;
	display: block;
}

.work__plus {
	position: absolute;
	top: 35%;
	opacity: 0;
	transition-property: opacity;
	transition-duration: 500ms;
}

.work__info {	
	position: absolute;
	background-color: #292f36;
	padding: 20px;
	left: 0;
	right: 0;
	bottom: 0;
	opacity: 0;
	transition-property: opacity;
	transition-duration: 500ms;
}

.work__text {
	font-size: 18px;
	font-family: "Quattrocento Sans";
	color: rgb(255, 255, 255);
	font-weight: bold;
	text-transform: uppercase;
	text-align-last: left;
	line-height: 1.5;
	margin: 0;
}

.work__button {
	@include button() {
		display: inline-block;
		width: 151px;
		height: 44px;
		margin-top: 60px;
	}
}

@media (max-width: 1140px) {
	.work__list {
		justify-content: space-evenly;
	}
}

@media (max-width: 768px) {
	.work__wrap {
		padding-top: 40px;
		padding-bottom: 50px;
	}

	.work__button {
		margin-top: 40px;
	}
}

@media (max-width: 480px) {

	.work__element {
		padding: 30px 0 0;
	}
}