.services {
	background-color: #fff;
}

.services__wrap {
	padding-top: 80px;
	padding-bottom: 160px;
}

.services__title {
	font-size: 36px;
	font-family: "Quattrocento Sans";
	color: rgb(41, 47, 54);
	font-weight: bold;
	text-transform: uppercase;
	line-height: 1.167;
	margin-left: 20px;
	margin-top: 0;
	position: relative;
	
	&::after {
		content: '';
		position: absolute;
		width: 10px;
		height: 35px;
		top: 0;
		left: -20px;
		background-color: rgb(225, 230, 234);
		display: block;
	}		
}

.services__list {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
}

.services__element {
	display: flex;
	flex-direction: column;
	text-align: center;
	// flex-basis: 23%;
	flex-basis: 262px;	
	margin-top: 80px;
	padding-left: 5px;
	padding-right: 5px;
}

.services__img {
	display: block;
	max-width: 48px;
	height: auto;
	margin: 0 auto;
}

.services__name {
	font-size: 17px;
	font-family: "Quattrocento Sans";
	color: rgb(41, 47, 54);
	font-weight: bold;
	text-transform: uppercase;
	line-height: 1.2;	
	margin: 35px 0 30px 0;
}

.services__desc {	
	font-size: 16px;
	color: rgb(141, 143, 146);
	line-height: 1.625;
	margin-top: 0;
}

@media (max-width: 1120px) {
	.services__list {
		justify-content: space-evenly;
	}
}

@media (max-width: 768px) {
	.services__wrap {
		padding-top: 40px;
		padding-bottom: 50px;
	}

	.services__title {
		margin-bottom: 0;
	}
}

@media (max-width: 480px) {

	.services__element {
		padding: 0;
		margin-top: 50px;
	}
}