.footer {
	background-color: #3a454d;
}

.footer__wrap {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 40px 15px;
}

.footer__copy {	
	font-size: 14px;		
	color: rgb(255, 255, 255);
	line-height: 1.857;
	margin: 0;	
}
.footer__list {
	display: flex;
	flex-wrap: wrap;
	list-style: none;
	margin: 0;
}

.footer__element {
	margin-right: 20px;
	&:last-child {
		margin-right: 0;
	}
}

.footer__link {
	font-size: 28px;
	font-family: "Socialico";
	color: rgb(255, 255, 255);
	line-height: 0.929;
	text-decoration: none;	
}

@media (max-width: 580px) {

	.footer__wrap {
		flex-direction: column;
		padding: 20px 5px;
	}

	.footer__copy {
		flex-basis: 100%;
		font-size: 13px;
	}

	.footer__list {
		flex-basis: 100%;
		padding-left: 0;
		margin-top: 10px;
	}
}