.touch {
	background-color: #fff;
}

.touch__wrap {
	padding-top: 60px;
	padding-bottom: 60px;
}

.touch__title {
	font-size: 36px;
	font-family: "Quattrocento Sans";
	color: rgb(41, 47, 54);
	font-weight: bold;
	text-transform: uppercase;
	line-height: 1.167;
	margin: 0 0 0 20px;
	position: relative;
	
	&::after {
		content: '';
		position: absolute;
		width: 10px;
		height: 35px;
		top: 0;
		left: -20px;
		background-color: rgb(225, 230, 234);
		display: block;
	}		
}

.touch__map {
	background-color: #e2e2e2;
	height: 345px;
}

.map__wrap {
	padding-top: 60px;
	padding-bottom: 100px;
}

.touch__block {
	display: flex;
	justify-content: space-between;
	position: relative;
}

.touch__left {
	flex-basis: 590px;
}

.touch__info {
	flex-direction: column;
	justify-content: flex-start;
}

.touch__name {
	font-size: 20px;
	font-family: "Quattrocento Sans";
	color: rgb(41, 47, 54);
	font-weight: bold;
	text-transform: uppercase;
	line-height: 1.3;
	margin: 0 0 0 10px;		
}

.touch__desc {	
	font-size: 16px;
	font-weight: normal;	
	color: rgb(141, 143, 146);
	line-height: 1.625;
	margin: 40px 0 0;
}

.touch__bold {
	font-weight: bold;
}

.touch__right {	
	flex-basis: 555px;
	background-color: #fff;
	margin-top: -195px;
	box-sizing: border-box;	
}

.touch__form {
 	padding: 30px;
}

.form__input {
	width: 240px;
	margin-bottom: 20px;
	display: inline-block;
	padding-right: 6px;

	&:nth-child(2) {
		padding-right: 0;
	}
	
	input {
		width: 240px;
		height: 44px;
		border-radius: 3px;
    background-color: #eaeef1;
    border: none;
    box-shadow: none;
		resize: none;
	}

  textarea {
		width: 488px;
		height: 180px;
    border-radius: 3px;
    background-color: #eaeef1;
    border: none;
    box-shadow: none;
		resize: none;
	}
}

label {
	display: block;
	font-size: 14px;
	line-height: 26px;
	text-transform: uppercase;
	color: rgb(141, 143, 146);
}

.form__button {
	@include button {
		width: 151px;
		height: 44px;
		border-radius: 3px;
		background-color: #23b7a4;
		border-color: #23b7a4;
		margin-top: 10px;
		font-weight: 400;
	}
}

@media (max-width: 1200px) {

	.form__input {
		width: 100%;

		input {
			width: 100%;
		}

		textarea {
			width: 100%;
		} 
	}
}

@media (max-width: 768px) {
	.touch__block {
		flex-direction: column;
	}

	.map__wrap {
		padding-top: 40px;
		padding-bottom: 30px;
	}

	.touch__wrap {
		padding: 40px 0;
	}

	.touch__right {
		flex-basis: 100%;
		margin-top: -320px;

		.touch__form {
			padding: 15px;
	
			.form__button {
				
				margin: 0 auto;
			}
		}	
	}
}

@media (max-width: 480px) {

	.touch__right {
		.touch__form {
			padding: 0;
			margin-top: 20px;
		}
	}
	

	.touch__desc {
		margin-top: 20px;
	}
}